import React, { Component } from "react";
import moment from "moment"
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, DatePicker, InputNumber} from "antd";

const { Option } = Select;
// const { TextArea } = Input;
//const InputGroup = Input.Group;

class CurrencyRateCreateForm extends Component {


  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.rate_date = values.rate_date.format("YYYY-MM-DD")
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { currencyRate } = this.props;
    const { loading, currencies } = currencyRate;
    const error = currencyRate.errorField ? currencyRate.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}> 
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="currencies.pageHeader" />}
            >
              {getFieldDecorator("id_currency", {
                //initialValue: 0,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={loading}
                  showSearch
                  optionFilterProp="children"
                  allowClear={true}
                  //disabled={used ? true : false}
                >
                  {currencies.map((data, key) => (
                    <Option key={key} value={data.id_currency}>
                      { data.currency_code + " (" + data.currency_name + ")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>
            
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="rateDate" />}
              validateStatus={
                error.rate_date || getFieldError("rate_date")
                  ? "error"
                  : null
              }
              help={
                error.rate_date
                  ? error.rate_date
                  : getFieldError("rate_date")
              }
            >
              {getFieldDecorator("rate_date", {
                initialValue: moment(new Date(), "YYYY-MM-DD"),
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <DatePicker
                  style={{ width: "100%" }}
                  allowClear={false}
                  // showTime={{ format: "HH:mm:ss" }}
                  // onChange={this.handleChangeStartDate}
                  // onOpenChange={this.handleChangeStartDate}
                  format="DD MMMM YYYY"
                  
                  // disabledDate={e => e.valueOf() > moment()}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="rate" />}
            >
              {getFieldDecorator("rate", {
                //initialValue: data.rate,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <InputNumber
                  formatter={value =>
                    `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                  }
                  parser={value => value.replace(/\$\s?|(,*)/g, "")}
                  style={{ width: "100%" }}
                  precision={2}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="fiscalRate" />}
            >
              {getFieldDecorator("fiscal_rate", {
                //initialValue: data.currency_rate,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <InputNumber
                  formatter={value =>
                    `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                  }
                  parser={value => value.replace(/\$\s?|(,*)/g, "")}
                  style={{ width: "100%" }}
                  precision={2}
                />
              )}
            </Form.Item>

          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedCurrencyRateCreateForm = Form.create({ name: "create_currency_rate" })(CurrencyRateCreateForm);

export default WrappedCurrencyRateCreateForm;
