import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import CurrencyRateCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/currency/currencyRate/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/currency-rates"

class CreateCurrencyRate extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createCurrencyRate } = this.props;
    createCurrencyRate();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.currencyRate.error) {
      notification("error", this.props.currencyRate.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.currencyRate.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { storeCurrencyRate } = this.props;
    storeCurrencyRate(value);
  };

  render() {
    // console.log(this.props);

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="currencyRates.add.title" />}
            style={{ margin:0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <CurrencyRateCreateForm
            submit={this.handleSubmit}
            currencyRate={this.props.currencyRate}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { currencyRate: state.currencyRate };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateCurrencyRate);
